.song-list-box {
  height: 100%;
  width: 100%;
}
.song-list-box ul li {
  display: flex;
  align-items: center;
  padding: 20px;
  color: #777;
}
.song-list-box ul li.active {
  color: #108ee9;
}
.song-list-box ul li.active .num {
  color: #108ee9;
  font-weight: bold;
}
.song-list-box ul li.active .text h3 {
  color: #108ee9;
}
.song-list-box ul li .num {
  font-size: 16px;
  margin-right: 20px;
}
.song-list-box ul li .red {
  color: #ff400b;
}
.song-list-box ul li .pic img {
  height: 40px;
  width: 40px;
  margin-right: 10px;
}
.song-list-box ul li .text {
  flex-grow: 1;
  overflow: hidden;
}
.song-list-box ul li .text h3 {
  width: 100%;
  font-size: 15px;
  color: #444;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: bold;
}
.song-list-box ul li .text div {
  width: 100%;
  font-size: 12px;
  margin-top: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.song-list-box .loading {
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin-top: -10px;
}
